<template>
  <!-- eslint-disable vue/html-indent -->

  <section
fluid
class="wordpress">
    <h1>Headless Wordpress</h1>

    <vue-markdown>
Experiments with WordPress API. The posts below are actually from a wordpress blog elsewhere. By going to https://public-api.wordpress.com/wp/v2/sites/areve200179678.wordpress.com/posts?_envelope=1 the page picks up the posts from https://areve200179678.wordpress.com

Actually though it is even easier because the [wpapi](https://www.npmjs.com/package/wpapi) module does some of the work.

The only difficulty I had is that `v-html` and `scoped` styles don't play nicely, so I have to scope my styles in a different way.

In my vue `mounted` method I have
``` es6
const wpcom = new WPAPI({
      endpoint: 'https://public-api.wordpress.com'
      // username: 'username',
      // password: 'password'
    })

    wpcom
      .posts()
      .namespace('wp/v2/sites/yourblogname.wordpress.com')
      .get()
      .then(data => {
        this.posts = data
      })
      .catch(console.error)

```
    </vue-markdown>

    <hr>
    <article
        v-for="post in posts"
        :key="post.id"
        class="post">
        <h2 v-html="post.title.rendered"/>
        <p v-html="post.content.rendered"/>
    </article>
  </section>
</template>
<script>
import VueMarkdown from 'vue-markdown'
import WPAPI from 'wpapi'

export default {
  name: 'OtherProjects',
  components: { VueMarkdown },
  data () {
    return {
      posts: {}
    }
  },
  mounted () {
    const wpcom = new WPAPI({
      endpoint: 'https://public-api.wordpress.com'
      // username: 'username',
      // password: 'password'
    })

    wpcom
      .posts()
      .namespace('wp/v2/sites/areve200179678.wordpress.com')
      .get()
      .then(data => {
        this.posts = data
      })
      .catch(console.error)
  }
}
</script>

<style lang="scss">

</style>



